import React from 'react';
import JW_Resume from '../assests/file_downloads/JW_Resume.pdf'
import downloadbtnImg from '../assests/buttons/download_button.png'

const DownloadButton = () => (
    <div style={{ marginTop: '15%' }}>
        <a href={JW_Resume} download target="_blank" style={{ textDecoration: "none", justifyContent: 'center', display: 'flex' }}>
            <img src={downloadbtnImg} style={{ maxWidth: '10%' }} />
        </a>
    </div>
);

export default DownloadButton;