import React from 'react';
import messages from '../../data/WelcomeMessages';
import KnowledgeList from '../KnowledgeTiles/KnowledgeList';
import logo from '../../logo.png'
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';

const bioText = "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua." +
    "Nulla at volutpat diam ut venenatis tellus in metus. Ut eu sem integer vitae justo eget magna fermentum. Integer enim neque volutpat ac tincidunt." +
    "Arcu risus quis varius quam quisque id diam. Mattis vulputate enim nulla aliquet porttitor lacus luctus accumsan tortor." +
    "Amet massa vitae tortor condimentum lacinia quis vel. Tristique senectus et netus et malesuada. Ante metus dictum at tempor commodo ullamcorper a. Morbi tristique senectus et netus et malesuada fames.";

const Loader = props => {
    const { messages } = props;
    // Default to the first message passed
    const [messageIndex, setMessageIndex] = React.useState(0);

    React.useEffect(() => {
        // Move on to the next message every `n` milliseconds
        let timeout;
        if (messageIndex < messages.length - 1) {
            timeout = setTimeout(() => setMessageIndex(messageIndex + 1), 3000);
        }
        return () => {
            clearTimeout(timeout);
        };
    }, [messages, messageIndex]);

    return <div style={{ color: 'plum' }}>
        <h3 style={{ fontSize: 20 }}>{messages[messageIndex]}</h3>
    </div>;
};

// Websites Navbar, displays routes defined in 'src/data/routes'
const WelcomeBox = () => (
    <div style={{ display: 'flex', justifyContent: 'center' }}>
        <Card style={{ width: '80%', marginTop: '5%', backgroundColor: 'gray', maxWidth: '800px', minWidth: '100px' }}>
            <CardContent>
                <div>
                    <div style={{ justifyContent: 'center', display: 'flex', marginTop: '3%' }}>
                        <img src={logo} style={{ width: '30%', maxWidth: '300px' }}></img>
                    </div>
                    <div style={{ justifyContent: 'center', display: 'flex' }} >
                        <Loader messages={messages} />
                    </div>
                    <h3 style={{ justifyContent: 'center', display: 'flex', fontSize: '12px' }}>{bioText}</h3>
                    <div style={{ justifyContent: 'center', display: 'flex', paddingTop: '2%' }}>
                        <KnowledgeList />
                    </div>
                </div>
            </CardContent>
        </Card>
    </div>
);

export default WelcomeBox;
